.about__me {
    padding: 110px 70px 100px;
    margin: 0;
}

.about__info {
    display: flex;
    padding-top: 66px;
    justify-content: space-between;
}

.about__section {
    max-width: 600px;
    margin-right: 30px;
}

.about__name {
    margin: 0 0 18px;
    font-weight: 400;
    font-size: 50px;
    line-height: 58px;
    letter-spacing: -2px;
}

.about__description {
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    margin: 0 0 26px;
}

.about__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    margin: 0 0 100px;
}

.about__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-decoration: none;
    color: #000000;
}

.about__avatar{
    width: 270px;
    height: 327px;
    border-radius: 10px;
    object-fit: cover;
}

@media screen and (max-width: 768px) {
    .about__me {
        padding: 100px 50px;
    }

    .about__section {
        max-width: 364px;
    }

    .about__name {
        font-size: 40px;
        line-height: 40px;
        letter-spacing: -1.6px;
    }

    .about__description {
        font-size: 12px;
        line-height: 18px;
        margin: 0 0 20px;
    }

    .about__text {
        margin-bottom: 100px;
        font-size: 12px;
        line-height: 18px;
    }

    .about__link {
        font-size: 14px;
        line-height: 17px;
    }

    .about__avatar {
        width: 255px;
        height: 307px;
    }
}

@media screen and (max-width: 550px) {
    .about__info {
        margin: 0;
        padding-top: 60px;
        align-items: center;
        flex-direction: column-reverse;
        gap: 40px;
    }

    .about__section {
        margin: 0;
    }

    .about__name {
        font-size: 30px;
        line-height: 36px;
        letter-spacing: -1.2px;
    }

    .about__description {
        font-size: 11px;
        line-height: 16px;
    }

    .about__text {
        margin-bottom: 40px;
        font-size: 11px;
        line-height: 16px;
    }

    .about__link {
        font-size: 14px;
        line-height: 17px;
    }

    .about__avatar {
        width: 292px;
        height: 352px;
    }
}

@media screen and (max-width: 480px) {
    .about__text {
        margin-bottom: 55px;
    }
    .about__me {
        padding: 70px 14px;
    }
}